import React, { Component } from 'react';
import _ from 'lodash';

const $ = require('jquery');

require('./Component2.css');

const textStepCode =
`{
  id: '1',
  message: 'Hello World',
  end: true,
}
`;

const optionsStepCode =
`{
  id: '1',
  options: [
    { value: 1, label: 'Number 1', trigger: '3' },
    { value: 2, label: 'Number 2', trigger: '2' },
    { value: 3, label: 'Number 3', trigger: '2' },
  ],
}
`;

const customStepCode =
`{
  id: '1',
  component: <CustomComponent />
  trigger: '2'
}
`;

const sections = [
  {
    title: 'Text Step',
    example: textStepCode,
    properties: [
      {
        name: 'id',
        type: 'String | Number',
        required: 'true',
        description: 'The step id. Required for any step',
      },
      {
        name: 'message',
        type: 'String',
        required: 'true',
        description: 'The text message',
      },
      {
        name: 'trigger',
        type: 'String | Number',
        required: 'false',
        description: 'The id of next step to be triggered',
      },
      {
        name: 'user',
        type: 'Boolean',
        required: 'false',
        description: 'if true indicate that you waiting a user type action. Note: In this case the message will be ignored',
      },
      {
        name: 'validator',
        type: 'Function',
        require: 'false',
        description: 'if user attribute is true you can pass a validator function to validate the text typed by the user',
      },
      {
        name: 'end',
        type: 'Boolean',
        required: 'false',
        description: 'if true indicate that this step is the last',
      },
    ],
  },
  {
    title: 'Options Step',
    example: optionsStepCode,
    properties: [
      {
        name: 'id',
        type: 'String | Number',
        required: 'true',
        description: 'The step id. Required for any step',
      },
      {
        name: 'options',
        type: 'Array',
        required: 'true',
        description: 'Array of options with { label, value, trigger } properties',
      },
      {
        name: 'end',
        type: 'Boolean',
        required: 'false',
        description: 'if true indicate that this step is the last',
      },
    ],
  },
  {
    title: 'Custom Step',
    example: customStepCode,
    properties: [
      {
        name: 'id',
        type: 'String | Number',
        required: 'true',
        description: 'The step id. Required for any step',
      },
      {
        name: 'component',
        type: 'Component',
        required: 'true',
        description: 'Custom React Component',
      },
      {
        name: 'user',
        type: 'Boolean',
        required: 'false',
        description: 'if true indicate that you waiting a user action. You must use the triggerNextStep prop when user execute the action',
      },
      {
        name: 'end',
        type: 'Boolean',
        required: 'false',
        description: 'if true indicate that this step is the last',
      },
    ],
  },
];

class Component2 extends Component {
  constructor(props) {
    super(props);

    this.renderSections = this.renderSections.bind(this);
  }

  componentDidMount() {
    $('pre code').each((i, block) => {
      hljs.highlightBlock(block);
    });
  }

  renderProperties(prop) {
    const { name, type, required, description } = prop;

    return (
      <tr key={prop.name}>
        <td className="blue-td">{name}</td>
        <td className="red-td">{type}</td>
        <td><code>{required}</code></td>
        <td>{description}</td>
      </tr>
    );
  }

  renderSections(section) {
    return (
      <div
        className="section"
        key={section.title}
      >
        <h2 style={{ marginTop: 32 }}>{section.title}</h2>
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Type</th>
              <th>Required</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            {_.map(section.properties, this.renderProperties)}
          </tbody>
        </table>
        <h4 style={{ padding: '6px 12px' }}>Example</h4>
        <pre>
          <code className="jsx">
            {section.example}
          </code>
        </pre>
      </div>
    );
  }

  render() {
    return (
      <div className="docs-component-2">
        {_.map(sections, this.renderSections)}
      </div>
    );
  }
}

export default Component2;



