import React from 'react';

require('./Component1.css');

const properties = [
  {
    name: 'botAvatar',
    type: 'PropTypes.string',
    defaultValue: <img src="data:image/svg+xml;base64," height="30" />,
    description: 'Bot image source',
  },
  {
    name: 'botBubbleColor',
    type: 'PropTypes.string',
    defaultValue: <code>#eee</code>,
    description: 'Color of bot bubble color',
  },
  {
    name: 'botFontColor',
    type: 'PropTypes.string',
    defaultValue: <code>#000</code>,
    description: 'Color of bot font color',
  },
  {
    name: 'contentStyle',
    type: 'PropTypes.object',
    defaultValue: '',
    description: 'The style object to use to override the scroll element',
  },
  {
    name: 'customStyle',
    type: 'PropTypes.object',
    defaultValue: '',
    description: 'The style object to use to override the custom step element',
  },
  {
    name: 'delay',
    type: 'PropTypes.number',
    defaultValue: <code>1000</code>,
    description: 'The delay time for send the messages',
  },
  {
    name: 'footerStyle',
    type: 'PropTypes.object',
    defaultValue: '',
    description: 'The style object to use to override the footer element',
  },
  {
    name: 'handleEnd({ steps, value })',
    type: 'PropTypes.func',
    defaultValue: '',
    description: 'The callback function when chat ends',
  },
  {
    name: 'inputStyle',
    type: 'PropTypes.object',
    defaultValue: '',
    description: 'The style object to use to override the input element',
  },
  {
    name: 'steps',
    type: 'PropTypes.array',
    defaultValue: '',
    description: 'The chatbot steps to display',
  },
  {
    name: 'style',
    type: 'PropTypes.object',
    defaultValue: '',
    description: 'The style object to use to override the root element',
  },
  {
    name: 'userAvatar',
    type: 'PropTypes.string',
    defaultValue: <img src="data:image/svg+xml;base64," width="30" />,
    description: 'User image source',
  },
  {
    name: 'userBubbleColor',
    type: 'PropTypes.string',
    defaultValue: <code>#baf5fd</code>,
    description: 'Color of user bubble color',
  },
  {
    name: 'userFontColor',
    type: 'PropTypes.string',
    defaultValue: <code>#000</code>,
    description: 'Color of user font color',
  },
];

const Component1 = () => (
  <div className="docs-component-1">
    <h2 style={{ marginTop: 32 }}>Properties</h2>
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Type</th>
          <th>Default</th>
          <th>Description</th>
        </tr>
      </thead>
      <tbody>
        {
          properties.map((prop) => {
            const { name, type, defaultValue, description } = prop;
            return (
              <tr key={prop.name}>
                <td className="blue-td">{name}</td>
                <td className="red-td">{type}</td>
                <td>{defaultValue}</td>
                <td>{description}</td>
              </tr>
            );
          })
        }
      </tbody>
    </table>
  </div>
);

export default Component1;



