import React from 'react';
import PropTypes from 'prop-types';
import ChatBot from 'react-simple-chatbot';

require('./Home.css');

const home = {
  title: 'REACT Simple Chatbot',
  description: 'A simple react chatbot component',
  docsLink: '/docs/installation',
  github: {
    user: 'lucasbassetti',
    repository: 'react-simple-chatbot',
  },
};

const Home = (props) => {
  const { title, description, docsLink, github } = home;
  return (
    <div className="home">
      <div className="column">
        <h1 className="title">{title}</h1>
        <p className="description">{description}</p>
        <div className="buttons">
          <div className="button">
            <a
              className="github-button"
              href={`https://github.com/${github.user}/${github.repository}`}
              data-style="mega"
              data-count-href={`/${github.user}/${github.repository}/stargazers`}
              data-count-api={`/repos/${github.user}/${github.repository}#stargazers_count`}
              data-count-aria-label="# stargazers on GitHub"
              aria-label={`Star ${github.user}/${github.repository} on GitHub`}
            >
              Star
            </a>
          </div>
          <div className="button">
            <a
              className="github-button"
              href={`https://github.com/${github.user}/${github.repository}/fork`}
              data-style="mega"
              data-count-href={`/${github.user}/${github.repository}/network`}
              data-count-api={`/repos/${github.user}/${github.repository}#forks_count`}
              data-count-aria-label="# forks on GitHub"
              aria-label={`Fork ${github.user}/${github.repository} on GitHub`}
            >
              Fork
            </a>
          </div>
        </div>
        <p className="documentation">
          <a
            className="docs-button"
            onClick={() => props.handleLink(docsLink)}
          >
            <i className="fa fa-book" />
            Documentation
          </a>
        </p>
      </div>
      <div className="column">
        <ChatBot
          style={{
            padding: 16,
            background: '#fff',
            border: 0,
            boxShadow: '0 0 100px #ddd',
            borderRadius: '10px',
            width: 280,
          }}
          botBubbleColor="#6E48AA"
          botFontColor="#fff"
          userBubbleColor="#9D50BB"
          userFontColor="#fff"
          contentStyle={{ overflow: 'hidden' }}
          footerStyle={{ display: 'none' }}
          steps={[
            {
              id: '1',
              message: 'Welcome to chatbot!',
              trigger: '2',
            },
            {
              id: '2',
              message: 'Check out the documentation!',
              trigger: '3',
            },
            {
              id: '3',
              options: [
                { value: 'got-it', label: 'Got it!', trigger: '4' },
              ],
            },
            {
              id: '4',
              message: 'Great!',
              trigger: '5',
            },
            {
              id: '5',
              message: 'I hope you enjoy it! :)',
              end: true,
            },
          ]}
        />
      </div>
    </div>
  );
};

Home.propTypes = {
  handleLink: PropTypes.func.isRequired,
};

export default Home;



